import {TaskType, UpdateDomainTaskModelType} from "features/todolistsList/tasks-reducer";
import {instance} from "common/api/common-api";
import {ResponseType} from "common/types/common.type";

export const todolistsAPI = {
    getTodolists() {
        return instance.get<Array<TodolistType>>('todo-lists')
    },
    createTodolist(title: string) {
        return instance.post<ResponseType<TodolistType>>('todo-lists', {title})
    },
    deleteTodolist(todolistId: string) {
        return instance.delete<ResponseType>(`todo-lists/${todolistId}`)
    },
    updateTodolist(todolistId: string, title: string) {
        return instance.put<ResponseType<{ item: TodolistType }>>(`todo-lists/${todolistId}`, {title})
    },
    getTasks(todolistId: string) {
        return instance.get<GetTasksResponseType>(`/todo-lists/${todolistId}/tasks`)
    },
    createTask(arg: AddTaskArgType) {
        return instance.post<ResponseType<TaskType>>(`/todo-lists/${arg.todolistId}/tasks`, {title: arg.title})
    },
    deleteTask(todolistId: string, taskId: string) {
        return instance.delete<ResponseType>(`/todo-lists/${todolistId}/tasks/${taskId}`)
    },
    updateTask(todolistId: string, taskId: string, model: UpdateTaskModelType) {
        return instance.put<ResponseType<{ item: TaskType }>>(`/todo-lists/${todolistId}/tasks/${taskId}`, model)
    }
}


export const TaskStatuses = {
    New: 0,
    InProgress: 1,
    Completed: 2,
    Draft: 3
} as const
export const TaskPriorities = {
    Low: 0,
    Middle: 1,
    Hi: 2,
    Urgently: 3,
    Later: 4
} as const
export const ResultCode = {
    Success: 0,
    Error: 1,
    Captcha: 10
} as const
type GetTasksResponseType = {
    items: Array<TaskType>
    totalCount: number
    error: string | null
}
export type TaskStatusesType = 0 | 1 | 2 | 3
export type TaskPrioritiesType = 0 | 1 | 2 | 3 | 4
export type UpdateTaskModelType = {
    title: string
    description: string
    status: TaskStatusesType
    priority: TaskPrioritiesType
    startDate: string
    deadline: string
}
export type AddTaskArgType = {
    todolistId: string
    title: string
}
export type UpdateTaskArgType = {
    todolistId: string,
    taskId: string,
    domainModel: UpdateDomainTaskModelType
}
export type TodolistType = {
    id: string
    title: string
    addedDate: string
    order: number
}