import React, {useCallback, useEffect} from "react";
import style from './style.module.css'
import {useSelector} from "react-redux";

import {
    FilterValuesType,
    todolistActions, todolistsThunks
} from "./todolists-reducer";
import {tasksThunks} from "./tasks-reducer";
import Grid from "@mui/material/Grid";
import {Todolist} from "./todolist/Todolist";
import {Navigate} from "react-router-dom";
import {useAppDispatch} from "common/hooks/hooks";
import {selectIsLoggedIn} from "features/auth/auth-selectors";
import {selectTodolists} from "features/todolistsList/todolist-selectors";
import {selectTasks} from "features/todolistsList/tasks-selectors";
import {AddItemForm} from "common/components";
import {TaskStatusesType} from "features/todolistsList/todolists.api";


type PropsType = {
    demo?: boolean
}
export const TodolistsList: React.FC<PropsType> = ({demo = false}) => {

    const todolists = useSelector(selectTodolists)
    const tasks = useSelector(selectTasks)
    const isLoggedIn = useSelector(selectIsLoggedIn)
    const dispatch = useAppDispatch()

    useEffect(() => {
        if (demo || !isLoggedIn) {
            return
        }
        dispatch(todolistsThunks.fetchTodolists())
    }, [])

    const removeTask = (todolistId: string, taskId: string) => {
        dispatch(tasksThunks.removeTask({todolistId, taskId}))
    }
    const addTask = useCallback((todolistId: string, title: string) => {
        dispatch(tasksThunks.addTask({todolistId, title}))
    }, [])
    const changeTaskStatus = useCallback((todolistId: string, taskId: string, status: TaskStatusesType) => {
        dispatch(tasksThunks.updateTask({todolistId, taskId, domainModel: {status}}))
    }, [])
    const changeTaskTitle = useCallback((todolistId: string, taskId: string, title: string) => {
        dispatch(tasksThunks.updateTask({todolistId, taskId, domainModel: {title}}))
    }, [])

    const removeTodolist = useCallback((id: string) => {
        dispatch(todolistsThunks.removeTodolist({id}))
    }, [])

    const addTodolist = useCallback((title: string) => {
        dispatch(todolistsThunks.addTodolist({title}))
    }, [])
    const changeTodolistFilter = useCallback((id: string, filter: FilterValuesType) => {
        dispatch(todolistActions.changeTodolistFilter({id, filter}))
    }, [])
    const changeTodolistTitle = useCallback((id: string, title: string) => {
        dispatch(todolistsThunks.changeTodolistTitle({id, title}))
    }, [])

    if (!isLoggedIn) {
        return <Navigate to={'/login'}/>
    }

    return (
        <div>
            <Grid container style={{padding: '20px'}}>
                <AddItemForm addItem={addTodolist}/>
            </Grid>
            <Grid container spacing={12}>
                {todolists.map((todolist) => {
                    return (
                        <Grid item key={todolist.id}>
                            <div className={style.todolist}>
                                <Todolist
                                    key={todolist.id}
                                    todolist={todolist}
                                    tasks={tasks[todolist.id]}
                                    removeTask={removeTask}
                                    changeTodolistFilter={changeTodolistFilter}
                                    addTask={addTask}
                                    changeTaskStatus={changeTaskStatus}
                                    removeTodolist={removeTodolist}
                                    changeTaskTitle={changeTaskTitle}
                                    changeTodolistTitle={changeTodolistTitle}
                                    demo={demo}
                                />
                            </div>
                        </Grid>
                    )
                })}
            </Grid>
        </div>
    )
}
