import {appActions, RequestStatusType} from "app/app-reducer";
import {handlerServerNetworkError} from "common/utils/handler-server-network-error";
import {TaskDomainType} from "./tasks-reducer";
import {createSlice, PayloadAction} from "@reduxjs/toolkit";
import {clearTasksAndTodolists} from "common/actions/common.actions";
import {todolistsAPI, TodolistType} from "features/todolistsList/todolists.api";
import {createAppAsyncThunk} from "common/utils";

const slice = createSlice({
    name: 'todolists',
    initialState: [] as TodolistDomainType[],
    reducers: {
        changeTodolistFilter(state, action: PayloadAction<{ id: string, filter: FilterValuesType }>) {
            const todolist = state.find(todolist => todolist.id === action.payload.id)
            if (todolist) todolist.filter = action.payload.filter
        },
        changeTodolistEntityStatus(state, action: PayloadAction<{ id: string, entityStatus: RequestStatusType }>) {
            const todolist = state.find(todolist => todolist.id === action.payload.id)
            if (todolist) todolist.entityStatus = action.payload.entityStatus
        },
        clearTodolists(state, action: PayloadAction<{}>) {
            return []
        },
    },
    extraReducers: builder => {
        builder
            .addCase(fetchTodolists.fulfilled, (state, action) => {
                return action.payload.todolists.map((todolist: TodolistType) => ({
                    ...todolist,
                    filter: 'all',
                    entityStatus: 'idle'
                }))
            })
            .addCase(removeTodolist.fulfilled, (state, action) => {
                const index = state.findIndex(todolist => todolist.id === action.payload.id)
                if (index > -1) state.splice(index, 1)
            })
            .addCase(addTodolist.fulfilled, (state, action) => {
                const newTodolist: TodolistDomainType = {
                    ...action.payload.todolist,
                    filter: 'all',
                    entityStatus: 'idle'
                }
                state.unshift(newTodolist)
            })
            .addCase(changeTodolistTitle.fulfilled, (state, action) => {
                const todolist = state.find(todolist => todolist.id === action.payload.id)
                if (todolist) todolist.title = action.payload.title
            })
            .addCase(clearTasksAndTodolists, (state, action) => {
                return []
            })
    }
})

const fetchTodolists = createAppAsyncThunk<{ todolists: TodolistType[] }, void>
('todolists/fetchTodolists', async (arg, thunkAPI) => {
    const {dispatch, rejectWithValue} = thunkAPI
    try {
        dispatch(appActions.setAppStatus({status: 'loading'}))
        const res = await todolistsAPI.getTodolists()
        // const todolists = res.data
        // dispatch(todolistActions.setTodolists({todolists}))
        dispatch(appActions.setAppStatus({status: 'succeeded'}))
        return {todolists: res.data}
    } catch (e) {
        handlerServerNetworkError(e, dispatch)
        return rejectWithValue(null)
    }
})
const removeTodolist = createAppAsyncThunk<{ id: string }, { id: string }>
('todolists/removeTodolist', async (arg, thunkAPI) => {
    const {dispatch, rejectWithValue} = thunkAPI
    try {
        dispatch(appActions.setAppStatus({status: 'loading'}))
        dispatch(todolistActions.changeTodolistEntityStatus({id: arg.id, entityStatus: 'loading'}))
        await todolistsAPI.deleteTodolist(arg.id)
        dispatch(appActions.setAppStatus({status: 'succeeded'}))
        return {id: arg.id}
    } catch (e) {
        handlerServerNetworkError(e, dispatch)
        return rejectWithValue(null)
    }
})
const addTodolist = createAppAsyncThunk<{ todolist: TodolistType }, { title: string }>
('todolists/addTodolist', async (arg, thunkAPI) => {
    const {dispatch, rejectWithValue} = thunkAPI
    try {
        dispatch(appActions.setAppStatus({status: 'loading'}))
        const res = await todolistsAPI.createTodolist(arg.title)
        if (res.data.resultCode === 0) {
            dispatch(appActions.setAppStatus({status: 'succeeded'}))
            return {todolist: res.data.data.item}
        } else {
            if (res.data.messages.length) {
                dispatch(appActions.setAppError(({error: res.data.messages[0]})))
                return rejectWithValue(null)
            } else {
                dispatch(appActions.setAppError({error: 'Some error occurred'}))
                return rejectWithValue(null)
            }
        }
    } catch (e) {
        handlerServerNetworkError(e, dispatch)
        return rejectWithValue(null)
    }
})
const changeTodolistTitle = createAppAsyncThunk<{ id: string, title: string }, { id: string, title: string }>
('todolists/changeTodolistTitle', async (arg, thunkAPI) => {
    const {dispatch, rejectWithValue} = thunkAPI
    try {
        dispatch(appActions.setAppStatus({status: 'loading'}))
        await todolistsAPI.updateTodolist(arg.id, arg.title)
        dispatch(appActions.setAppStatus({status: 'succeeded'}))
        return {id: arg.id, title: arg.title}
    } catch (e) {
        handlerServerNetworkError(e, dispatch)
        return rejectWithValue(null)
    }
})

export const todolistsReducer = slice.reducer
export const todolistActions = slice.actions
export const todolistsThunks = {fetchTodolists, removeTodolist, addTodolist, changeTodolistTitle}


//types
export type TodolistDomainType = TodolistType & {
    filter: FilterValuesType
    entityStatus: RequestStatusType
}
export type FilterValuesType = 'all' | 'active' | 'completed'
export type TasksStateType = {
    [key: string]: TaskDomainType[]
}