import React, {useEffect} from 'react';
import {ButtonAppBar} from "common/components/buttonAppBar/ButtonAppBar";
import Container from '@mui/material/Container';
import {TodolistsList} from "features/todolistsList/TodolistsList";
import {Route, Routes} from "react-router-dom";
import {Login} from "features/auth/Login";
import {useSelector} from "react-redux";
import CircularProgress from '@mui/material/CircularProgress';
import {initializeAppTC} from "./app-reducer";
import {useAppDispatch} from "common/hooks/hooks";
import {selectSetAppInitialized} from "app/app-selectors";
import style from './style.module.css'

type PropsType = {
    demo?: boolean
}
export const App = ({demo = false}: PropsType) => {
    const isInitialized = useSelector(selectSetAppInitialized)
    const dispatch = useAppDispatch()
    useEffect(() => {
        if (!demo) {
            dispatch(initializeAppTC())
        }
    }, [])

    if (!isInitialized) {
        return <div style={{position: 'fixed', top: '30%', textAlign: 'center', width: '100%'}}>
            <CircularProgress/>
        </div>
    }

    return (
        <div className={style.container}>
            <ButtonAppBar/>
            <Container fixed>
                <Routes>
                    <Route path='*' element={<h1>404: PAGE NOT FOUND</h1>}/>
                    <Route path='/todolist' element={<TodolistsList demo={demo}/>}/>
                    <Route path='/login' element={<Login/>}/>
                </Routes>
            </Container>
        </div>
    );
}

