import {appActions} from "app/app-reducer";
import {Dispatch} from "redux";
import {ResponseType} from "common/types/common.type";


export const handlerServerAppError = <T>(data: ResponseType<T>, dispatch: ThunkDispatch) => {
    if (data.messages.length) {
        dispatch(appActions.setAppError({error: data.messages[0]}))
    } else {
        dispatch(appActions.setAppError({error: 'Some error occurred'}))
    }
    dispatch(appActions.setAppStatus({status: 'failed'}))
};

//types
type ThunkDispatch = Dispatch<ActionsType>
type ActionsType = ReturnType<typeof appActions.setAppError> | ReturnType<typeof appActions.setAppStatus>
